
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
body {
  margin: 0;
}
nav {
  border-bottom: 32px solid #42433E;
}
.logo-container {
  width: 100%;
  max-width: 1160px;
  margin: auto;
  text-align: center;
}
.logo-container img {
  margin-left: 200px;
  padding: 10px 0;
}
@media (max-width: 767px) {
nav {
    border-width: 20px;
}
nav img {
    max-width: 100%;
}
}
@media (max-width: 1280px) {
.logo-container {
    overflow: hidden;
}
.logo-container img {
    overflow: hidden;
    margin-left: 0px;
}
}
#footer {
  width: 100%;
  /* height: 60px; */
  height: 40px;
  text-align: center;
  /* padding-top: 20px; */
  padding: 10px 0;
  color: #fff;
  font-style: normal;
  font-weight: 600;
  font-size: 0.83em;
